














import { Component, Vue } from "vue-property-decorator";
import local from "@/data/local";
import { Word } from "@/types/word";
import ExpressWord from "@/components/ExpressWord.vue";
import UnSavedButton from "@/components/UnSavedButton.vue";

@Component({
  components: {
    ExpressWord,
    UnSavedButton,
  },
})
export default class Saved extends Vue {
  saved: Word[] = [];
  waiting = true;

  mounted(): void {
    this.refresh();
  }

  refresh(): void {
    this.waiting = true;
    this.saved = local.getAll();
    this.waiting = false;
  }
}
