
























import { Component, Vue } from "vue-property-decorator";
import ExpressWord from "@/components/ExpressWord.vue";
import SavedButton from "@/components/SavedButton.vue";
import SearchForm from "@/components/SearchForm.vue";
import api from "@/data/apis";
import { Word } from "@/types/word";
import { APIResponse } from "@/types/api-response";

@Component({
  components: {
    SearchForm,
    ExpressWord,
    SavedButton,
  },
})
export default class Search extends Vue {
  results: APIResponse<Word[]> = {} as APIResponse<Word[]>;
  waiting = true;
  // mounted(): void {
  //   this.refresh();
  // }
  async search(args: string): Promise<void> {
    this.waiting = true;
    this.results = await api.search(args);
    this.waiting = false;
  }
}
